import React from 'react';
import { Link } from 'react-router-dom';

const ImageOverlay = ({ image, close, prevImage, nextImage, stats }) => {
    return image ? 
        <div className="image-overlay">
            <header>
                <span>
                    <Link to={`/project/${image.project.id}`}>{image.project.title}</Link>
                </span>
                <span><small>{stats}</small></span>
                <span onClick={close}></span>
            </header>
            <section>
                <img src={`http://demkina.me/images/original/${image.project.id}/${image.name}`} alt=""/>
                <div className="arrow arrow-left" onClick={prevImage}></div>
                <div className="arrow arrow-right" onClick={nextImage}></div>
            </section>
        </div> 
        : null
}

export default ImageOverlay;
