import React from 'react';
import Gallery from 'react-photo-gallery';
import { photos } from '../api/photos';
import { getImage, columns } from '../utils';
import GalleryImage from './GalleryImage';
import ImageOverlay from './ImageOverlay';

const getImagesForPage = (project, onClick) => {
    if (!project) {
        return photos
            .map(photo => getImage(photo))
            .map(photo => ({ ...photo, onClick: () => onClick(photo.name) }))
            .map(a => [Math.random(), a])
            .sort((a, b) => a[0] - b[0])
            .map(a => a[1]);
    } else {
        return photos
            .filter(photo => photo.project === project)
            .map(photo => getImage(photo))
            .map(photo => ({ ...photo, onClick: () => onClick(photo.name) }));
    }
}

class IndexPage extends React.Component {
    state = {
        images: [],
        imagesNames: []
    }

    onClick = name => this.props.history.push(`#${name}`);
    getCurrIndex = name => this.state.imagesNames.indexOf(name);
    getNextIndex = name => Math.min(this.getCurrIndex(name)+1, this.state.imagesNames.length-1);
    getPrevIndex = name => Math.max(this.getCurrIndex(name)-1, 0);

    setImage = name => this.props.history.push(`#${name}`);
    
    componentDidMount() {
        const images = getImagesForPage(this.props.match.params.project, this.onClick);
        const imagesNames = images.map(image => image.name);
        this.setState({ images, imagesNames });
    }

    componentDidUpdate(prevProps, prevState) {
        if (prevProps.match.params.project !== this.props.match.params.project) {
            const images = getImagesForPage(this.props.match.params.project, this.onClick);
            const imagesNames = images.map(image => image.name);
            this.setState({ images, imagesNames });
        }
    }

    render() {
        const hash = this.props.location.hash.slice(1);
        const showImage = hash.length > 0;
        const currIndex = this.getCurrIndex(hash);
        const stats = `${currIndex + 1} / ${this.state.imagesNames.length}`;
        const prevImage = this.state.imagesNames[Math.max(0, currIndex-1)];
        const nextImage = this.state.imagesNames[Math.min(this.state.imagesNames.length-1, currIndex+1)];

        return <>
            <Gallery photos={this.state.images} direction={'column'} renderImage={GalleryImage} columns={columns} />
            { showImage ? <ImageOverlay 
                prevImage={() => this.setImage(prevImage)}
                nextImage={() => this.setImage(nextImage)}
                stats={stats}
                image={getImage(hash)} 
                close={() => this.props.history.push('#')}/> : null }
        </>
    }
}

export default IndexPage;
