import React from 'react';

const GalleryImage = ({ index, left, top, photo }) => {
    const { width, height, project, name, ...props } = photo;
    return <>
        <div key={`gi_${index}`} style={{ top, left}} className="gallery-image">
            <img
                {...props}
                alt={`Изображение из серии "${project.title}"`}
                src={`http://demkina.me/images/compressed/${project.id}/${name}`}
                width={width + 4}
                height={height + 4}
            />
        </div>
    </>
}

export default GalleryImage;
